@import url('https://fonts.googleapis.com/css2?family=Tiro+Gurmukhi:ital@0;1&display=swap');

/* nulling styles */
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box; /* element height and width will include padding and margin */
  list-style: none; /* sets the styles for the marker */
  text-decoration: none; /* responsible for text formatting */
}

:root { /* creating a variable */
  --color-bg: radial-gradient(at bottom, #F8F8FF, #6A5ACD);
  --color-bg-variant: #695ea5;
  --color-primary: #B0E0E6;
  --color-primary-variant: #483D8B;
  --color-main: #FFFFFF;
  --color-light: #E6E6FA;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 95%;

  --transition: all 400ms ease;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Tiro Gurmukhi', serif;
  background: var(--color-bg);
  color: var(--color-main);
  line-height: 1.7;
}

/* ===========GENERAL STYLES===========*/

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1, h2, h3, h4, h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section > h2, section > h5, section > h1 {
  text-align: center;
  color: var(--color-primary-variant);
  margin-bottom: 1rem;
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}

.btn {
  background: var(--color-primary-variant);
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.78rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover; /* The object-fit property specifies how the element should fit the container. */
}

/* ===========MEDIA QUERIES (MEDIUM DEVICES)=========== */

@media screen and (max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }

  .section {
    margin-top: 6rem;
  }
}

/* ===========MEDIA QUERIES (SMALL DEVICES)=========== */

@media screen and (max-width: 600px) {
  .container {
    width: var(--container-width-sm);
  }
  .section {
    margin-top: 5rem;
  }

  .section > h2 {
    margin-bottom: 2rem;
  }

}
